@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
body{margin:0}

.question{width:450px;position:relative}.question__image{position:absolute;top:-130px;right:0}.question__flag img{width:150px;border:1px #cdcdd3 solid}.question__text{font-weight:700;color:#2F527B;font-size:24px;margin:20px;margin-top:40px}.question__answer{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-content:center;align-content:center;margin:20px;min-height:56px;border:1px #6066D0 solid;border-radius:12px;color:#6066D0;text-align:left;cursor:pointer}.question__answer:hover{background:#F9A826;color:white;border:1px solid #F9A826}.question__answer-unactive{pointer-events:none}.question__answer-correct{background:#60BF88;border:1px #60BF88 solid;color:white}.question__answer-incorrect{background:#EA8282;border:1px #EA8282 solid;color:white}.question__answer p{margin:auto 15px}.question__next{text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;margin:auto 20px 10px auto;width:116px;height:56px;border-radius:12px;background:#F9A826;color:white;cursor:pointer}.question__next:hover{background:#f09607}.question__next:active{background:#dc8a06}@media (max-width: 550px){.question{width:80vw;min-height:75vh}.question__flag img{width:100px}.question__text{font-size:16px}.question__answer{margin:10px}.question__next{margin-right:10px}.question__image img{width:120px}}@media (max-width: 320px){.question__image{display:none}}

.results{width:400px;min-height:400px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-content:center;align-content:center;text-align:center;color:#2F527B}.results .green{color:#60BF88}.results .red{color:#EA8282}.results__button{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;margin:0 auto;width:50%;height:60px;border:2px #2F527B solid;border-radius:12px;color:#2F527B;cursor:pointer}.results__button:hover{background:#F9A826;color:white;border:2px #F9A826 solid}

.quiz{max-width:-webkit-min-content;max-width:min-content;margin:auto}.quiz__title{color:white;margin-bottom:10px}.quiz__game{background:white;border-radius:24px;padding:10px}@media (max-width: 550px){.quiz{height:90vh;width:95vw}.quiz__title{font-size:20px}}

*{font-family:'Poppins', sans-serif;margin:0}.App{background-image:url(/country-quiz/static/media/background.41dbc550.png);background-size:cover;background-position:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center;min-height:100vh}

